<template>
  <div class="nav-bar sticky-top shadow-sm rounded d-flex align-items-center">
    <div class="container d-flex justify-content-between ">
      <div class="logo my-2">
        <router-link to="/index" >
          <img src="@/assets/img/logo.jpeg" class="card-img-top" alt />
        </router-link>
      </div>
      <div class="d-block d-sm-flex align-items-center justify-content-between my-2 sticky-top">
        <div>
          <div class="menu d-flex text-right" >
            <li>
              <router-link class="mx-4" to="/index">Home</router-link>
            </li>
            <!--  關於我們 -->
            <li>
              <router-link class="" to="/about">Contact</router-link>
            </li>
            <!--  看看大家都賣什麼 -->
            <li>
              <router-link class="mx-4"  to="/products">Products</router-link>
            </li>
            <!-- 購物車 -->
            <li >
              <router-link to="/cart">Carts({{ header_cart.length }})</router-link>
            </li>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      header_cart: {},
      uuid: process.env.VUE_APP_UUID
    }
  },
  created () {
    this.getcartLength()
    this.$bus.$on('updateTotal', () => {
      this.getcartLength()
    })
  },
  updated () {
    this.getcartLength()
  },
  beforeDestroy () {
    this.$bus.$off('updateTotal', () => {
      this.getcartLength()
    })
  },
  methods: {
    getcartLength () {
      const url = `${process.env.VUE_APP_APIPATH}/api/${this.uuid}/ec/shopping`
      this.axios.get(url)
        .then((res) => {
          this.header_cart = res.data.data
        })
        .catch((err) => {
          console.log(err)
        })
    }
  }

}
</script>
<style lang="stylus"></style>
